<template>
  <div id="raffle" class="d-flex justify-center align-center">
    <div class="game d-flex justify-center align-center">
      <!-- Âè≥ÂÅ¥Ê¨Ñ -->
      <div class="gameSetFrame">
        <v-icon class="gameSet" @click.stop="dialogSet = true">mdi-cog-outline</v-icon>
        <v-icon class="gameSet mt-2">mdi-history</v-icon>
      </div>
      <!-- Ë®≠ÂÆöÂ∞çË©±Ê°Ü -->
      <v-dialog
        v-model="dialogSet"
        max-width="400"
        persistent
        scrollable
      >
        <v-card>
          <!-- Ê®ôÈ°å -->
          <v-card-title class="d-flex justify-center font-weight-bold orange--text">
            ÊäΩÁçéË®≠ÂÆö
          </v-card-title>
          <!-- ÊñáÂ≠ó„ÄÅÈÅ∏È†Ö -->
          <vue-scroll>
            <v-card-text class="d-flex justify-center align-center flex-column pa-0">
              <!-- ÈÅ∏ÊìáÊñπÂºè -->
              <v-radio-group v-model="radioSet">
                <v-radio
                  label="ÂèØÈáçË§áÊäΩ‰∏≠Âêå‰∏ÄÁ¥ôÊ¢ù"
                  value="w1"
                  color="dhblue"
                ></v-radio>
                <v-radio
                  label="‰∏çÈáçË§á"
                  value="w2"
                  color="dhblue"
                ></v-radio>
              </v-radio-group>
              <!-- TODO ‰ª•ÂæåÊúâÊ©üÊúÉÂÜçÂÅöÂàÜ ÁçéÂìÅ Âíå ÂêçÂñÆ È†ÖÁõÆ -->
              <!--È†ÖÁõÆÂÖßÂÆπ -->
              <v-text-field
                v-for="(input,idx) in inputs"
                :key="idx"
                :label="input.num"
                v-model="input.item"
                color="success"
                style="width:60%;"
              >
                <!-- Á¥ôÊ¢ù > 3 ÊúÉÂá∫ÁèæÂèØÂà™Èô§ icon -->
                <template
                  v-if="inputs.length >= 3"
                  v-slot:append-outer
                >
                  <v-icon
                    class="inputDel"
                    @click="deletInput(idx)"
                  >
                    mdi-delete-forever
                  </v-icon>
                </template>
              </v-text-field>
              <!-- Â¢ûÂä† -->
              <v-icon
                class="inputAdd"
                @click="addInput"
              >
                mdi-plus-circle-outline
              </v-icon>
            </v-card-text>
          </vue-scroll>
          <!-- Á¢∫ÂÆöÈàï -->
          <v-card-actions class="d-flex justify-center pb-4">
            <v-btn
              color="green darken-1"
              style="height:30px;font-size:0.95rem;"
              text
              @click="submitInput"
            >
              Á¢∫ÂÆö
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!-- ÁÆ±Â≠ê class shake-constant shake-hard -->
      <div id="box" ref="box" class="d-flex justify-center">
        <!-- ÁõíÂ≠êÊ°Ü -->
        <div id="border">
        <div
          v-for="(input,idx) in inputs"
          :key="idx"
          class="paper"
          :style="input.position"
          @click="keep"
        >
          <span class="text">{{input.item}}</span>
        </div>
        </div>
      </div>
      <!-- ÈñãÂßãÈçµ -->
      <div
        class="startBig d-flex justify-center align-center"
        ref="startBig"
        @click="start($event)"
      >
        GO
      </div>
      <!-- ÈÅÆÊâã -->
      <span ref="hand" class="maskHand" @click="open">ü§ö</span>
      <!-- ÈñãÂïüÊèêÈÜí -->
      <span v-if="openText" class="openText">ÈªûÊâãÊè≠Êõâ</span>
      <span v-if="keepText" class="openText">ÈªûÁ¥ôÁπºÁ∫å</span>
    </div>
  </div>
</template>

<script>
// Ê≠£ÂâáË°®ÈÅîÂºèÔºåÂè™ÁïôÊï∏Â≠ó
const number = (str) => {
  return str.replace(/\D/g, '')
}
// ÂçÄÈñìÈö®Ê©üÊï∏
const rand = (min, max) => {
  return Math.round(Math.random() * (max - min) + min)
}
// ÊäΩÁ®ÆÁ¥ôÊ¢ùÊ®ôË®ò
// let n = 0

export default {
  name: 'Raffle',
  data: () => ({
    dialogSet: false,
    dialogResult: false,
    openText: false,
    keepText: false,
    result: '',
    // È†êË®≠ÈÅ∏ÊìáÊñπÂºè
    radioSet: 'w2',
    inputs: [
      {
        num: 'Á¥ôÊ¢ù1',
        item: 'üòä',
        position: {
          transform: 'skew(-20deg,-10deg) rotate(-270deg)',
          left: '15%',
          bottom: '2%',
          background: '#E12E4B'
        }
      },
      {
        num: 'Á¥ôÊ¢ù2',
        item: 'Ë´ãÈªûÈÅ∏',
        position: {
          transform: 'skew(-10deg,-10deg) rotate(-60deg)',
          left: '42%',
          bottom: '2%',
          background: '#F9e54E'
        }
      },
      {
        num: 'Á¥ôÊ¢ù3',
        item: 'Âè≥‰∏äËßí',
        position: {
          transform: 'skew(-5deg,-5deg) rotate(-280deg)',
          left: '65%',
          bottom: '2%',
          background: '#5BBDC8'
        }
      }
    ]
  }),
  methods: {
    addInput () {
      const n = this.$data.inputs.length
      this.$data.inputs.push({ num: `Á¥ôÊ¢ù${n + 1}`, item: '' })
    },
    deletInput (idx) {
      const inputs = this.$data.inputs
      let index = 'ÂêÑÂÄã input ÁöÑÁ¥¢ÂºïÁõ¥'
      // *console.log(`Âà™Èô§Á¨¨ ${idx} ÂÄã`)
      console.log(`Á¥îÁ¥ôÊ¢ùÊï∏Â≠óÔºö${number(inputs[idx].num)}`)
      console.log(`Âà™Èô§ [${inputs[idx].num}]`)
      // idx Ë¢´Âà™Èô§ 1ÔºåÂæåÈù¢ÂÖ®ÈÉ®ÂæÄÂâç -1 Á¥¢Âºï
      for (const input in inputs) {
        if (input > idx) {
          // console.log(idx)
          // input ÁÇ∫Á¥¢ÂºïÂÄº
          index = input
          // Âõ†ÁÇ∫Á¥¢ÂºïÂÄºÊòØÂæû 0 ÈñãÂßãÔºåÊâÄ‰ª•ÈÄôÊ®£Ë®≠Ë®àÂâõÂâõÂ•Ω Â≠óÈù¢‰∏äÊúÉÊòØ -1 ÁöÑÁãÄÊ≥Å
          inputs[input].num = `Á¥ôÊ¢ù${index}`
        }
      }
      // *Âà™ÊéâË©≤ input (ÂæåÂà™ÔºöÂÖàÂæåÈ†ÜÂ∫èÊúâÂ∑Æ)
      inputs.splice(idx, 1)
    },
    submitInput () {
      this.$data.dialogSet = false
      const inputs = this.$data.inputs
      let i = 0
      let j = i
      let k = j
      let bgColor = ''
      for (const input of inputs) {
        // ÈáçÁΩÆ k
        j = i
        k = (j %= 3)
        // Âà§Êñ∑È°èËâ≤
        k === 0 ? bgColor = '#E12E4B' : k === 1 ? bgColor = '#F9e54E' : bgColor = '#5BBDC8'
        // style Ë®≠ÂÆö
        input.position = {
          transform: `skew(-${rand(5, 15)}deg,-${rand(0, 15)}deg) rotate(-${rand(45, 270)}deg)`,
          left: `${rand(3, 77)}%`,
          bottom: `${rand(0, 20)}%`,
          background: `${bgColor}`
        }
        i++
      }
    },
    // ÈñãÂßãÊåâÈàï
    start (e) {
      const papers = document.getElementsByClassName('paper')
      // ÊåâÈàïÂ§±Êïà
      e.target.style.pointerEvents = 'none'
      // ÊåâÈàïÈö±Ëóè
      e.target.style.opacity = '0'

      setTimeout(() => {
        // ÊôÉÂãïÁÆ±Â≠ê
        this.$refs.box.classList.add('shake-constant', 'shake-hard')
        // ÊôÉÂãïÁ¥ôÊ¢ù„ÄÅÂ≠óËΩâÁôΩËâ≤
        for (const paper of papers) {
          paper.style.transform = `skew(-${rand(5, 15)}deg,-${rand(0, 15)}deg) rotate(-${rand(45, 270)}deg)`
          paper.style.left = `${rand(3, 77)}%`
          paper.style.bottom = `${rand(0, 20)}`
          // paper.style.transition = '1.8s'
        }
      }, 180)

      // TODO È¶¨Ë≥ΩÂÖãÊäΩ‰∏≠ÁöÑÈÇ£ÂºµÔºåÊäΩ‰∏≠ÂæåÁßªÈô§Ë©≤item
      setTimeout(() => {
        // Èö®Ê©üÊäΩÂèñÁ¥ôÊ¢ùËÆäÊï∏
        const one = rand(1, papers.length) - 1
        console.log(`ÊäΩÂà∞Á¨¨ ${one} Âºµ`)
        // ÂÅúÊ≠¢ÊôÉÂãïÁÆ±Â≠ê
        this.$refs.box.classList.remove('shake-constant', 'shake-hard')
        // Á¥ôÊ¢ù‰∏äÂçá
        papers[one].style.color = 'white'
        papers[one].style.left = '50%'
        papers[one].style.transform = 'translateX(-50%)'
        papers[one].firstChild.style.filter = 'blur(3px)'
        // Á¥ôÊ¢ùÊ®ôË®ò one
        papers[one].classList.add('one')
        // ‰ª•Ëû¢ÂπïÂ§ßÂ∞èÔºåÂà§Êñ∑Á¥ôÊ¢ù‰ΩçÁΩÆ
        window.innerWidth >= 1025 ? papers[one].style.bottom = '70%' : (window.innerWidth < 1025 && window.innerWidth) > 600 ? papers[one].style.bottom = '90%' : papers[one].style.bottom = '125%'

        // ÈÅÆÊâãÈÄ≤Â†¥„ÄÅÊèêÁ§∫ÊñáÂ≠ó
        this.$data.openText = true
        this.$refs.hand.classList.add('changeY')
      }, 2000)
    },
    open () {
      const one = document.getElementsByClassName('one')
      // ÈÅÆÊâãÈÄÄÂ†¥„ÄÅÊèêÁ§∫ÊñáÂ≠ó
      this.$data.openText = false
      this.$data.keepText = true
      this.$refs.hand.classList.remove('changeY')
      // Á¥ôÊ¢ùÊîæÂ§ß„ÄÅÂèØÈªûÊìä
      one[0].style.transform = 'translateX(-50%) scale(2)'
      one[0].style.color = 'black'
      one[0].style.pointerEvents = 'auto'
      one[0].firstChild.style.filter = 'blur(0px)'
      console.log(one)
      // n = 0
      // TODO ÂÅöOKÁöÑÊåâÈàï Á¥ôÊ¢ùÊ∂àÂ§±!!(‰∏ÄÂÆöË¶ÅÂÅöÂà∞Âî∑ÔºåÊâçÊ®£ÊâçËÉΩÊ∫ñÁ¢∫ÊäìÂà∞Ë¶ÅÊîæÂ§ßÁ¥ôÊ¢ù) ÂÖ¨Â∏ÉÁµêÊûú ÊåâÈàïÂá∫Áèæ
      // this.$refs.startBig.style.opacity = '1'
      // this.$refs.startBig.style.pointerEvents = 'auto'
    },
    // ËøîÂõûÈçµ
    keep () {
      const one = document.getElementsByClassName('one')
      // ÁßªÈô§Á¥ôÊ¢ù„ÄÅÊèêÁ§∫ÊñáÂ≠ó
      this.$data.keepText = false
      one[0].style.pointerEvents = 'none'
      one[0].style.transition = '0.3s'
      one[0].style.opacity = '0'
      setTimeout(() => {
        one[0].remove()
      }, 300)
      // ÊåâÈàïÂá∫Áèæ
      this.$refs.startBig.style.opacity = '1'
      this.$refs.startBig.style.pointerEvents = 'auto'
    }
  }
}
</script>
